.tablero {
    width: 80%;
    display: grid;
    grid-template-columns: auto repeat(5, 1fr); /* Una columna para los d√≠as y 6 para las horas */
    grid-template-rows: repeat(17, auto); /* 15 filas para las horas */
    gap: 2px; /* Espacio entre las celdas */
  }
  
  .celda {
    border: 1px solid black;
    padding: 5px;
    text-align: center;
  }



